<template>
  <div>
    <el-table :data="ArcticleData" style="width: 100%">
      <el-table-column label="标题" width="180">
        <template #default="scope">
          <el-input v-model="scope.row.title"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="描述" width="180">
        <template #default="scope">
          <el-input v-model="scope.row.describe"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="md文件上传">
        <template #default="scope">
          <el-upload
            class="upload-demo"
            :action="`${baseUrl}/administer/article/md`"
            :with-credentials="true"
            :limit="1"
            :on-success="
              function (res) {
                //这里接受res数据，并调用upMddata传入res和id
                upMddata(res, scope.row._id);
              }
            "
            :before-upload="mdBforeUpload"
            :show-file-list="false"
          >
            <el-button type="primary" size="mini">重新上传</el-button>
          </el-upload>
        </template>
      </el-table-column>
      <el-table-column label="重新上传封面" width="180">
        <template #default="scope">
          <div
            style="
              display: flex;
              justify-content: space-between;
              align-items: center;
            "
          >
            <el-image
              style="width: 40px; height: 40px"
              :src="baseUrl + scope.row.coverUrl"
              :preview-src-list="[baseUrl + scope.row.coverUrl]"
            >
            </el-image>
            <el-upload
              :action="`${baseUrl}/administer/article/cover`"
              :with-credentials="true"
              :limit="1"
              :on-success="
                function (res) {
                  coverupMddata(res, scope.row._id);
                }
              "
              :before-upload="coverBforeUpload"
              :show-file-list="false"
            >
              <el-button type="warning" size="mini">封面上传</el-button>
            </el-upload>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template #default="scope">
          <el-button
            type="primary"
            plain
            size="mini"
            @click="
              updataArcilec(scope.row._id, {
                title: scope.row.title,
                describe: scope.row.describe,
              })
            "
            >修改</el-button
          >
          <el-button
            type="danger"
            size="mini"
            @click="deldetarticle(scope.row._id)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "ArctcleLink",
  data() {
    return {
      ArcticleData: [], //文章数据数组
    };
  },
  methods: {
    //md文件上传成功的钩子函数
    upMddata(res, id) {
      //上传失败
      if (res.code) return this.$message.error(res.message);
      // this.articleForm.mdUrl = res.mdUrl; //得到md文件地址存储到mdUrl中
      //成功后调用并传入id和md文件地址
      this.updataArcilec(id, { mdUrl: res.mdUrl });
    },
    //md文件上传前的钩子函数
    mdBforeUpload(file) {
      if (/^(.+)\.md$/.test(file.name)) {
        return true;
      } else {
        this.$message.error("请上传文件后缀名为md文件类型");
      }
    },
    //封面图片上传成功的钩子函数
    coverupMddata(res, id) {
      //上传失败
      if (res.code) return this.$message.error(res.message);
      this.updataArcilec(id, { coverUrl: res.coverUrl });
    },
    //封面图片上传前的钩子函数

    coverBforeUpload(file) {
      if (file.status === "ready") {
        const isJPG = file.raw.type === "image/jpeg";
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error("上传头像图片只能是 JPG 格式!");
        }
        if (!isLt2M) {
          this.$message.error("上传头像图片大小不能超过 2MB!");
        }
        return isJPG && isLt2M;
      } else {
      }
    },
    //修改文章数据
    async updataArcilec(id, doc) {
      let { data } = await this.$axios({
        method: "POST",
        url: "/administer/article/updata",
        data: { id, doc },
      });
      this.getArticles(); //修改文章后获取新的文章数据
      this.$message.success(data.message);
    },
    //删除文章数据
    async deldetarticle(id) {
      let { data } = await this.$axios({
        method: "delete",
        url: "/administer/article/deldet",
        data: { id },
      });
      // console.log(data);
      this.getArticles(); //删除文章后获取新的文章数据
      this.$message.success(data.message);
    },
    //获取全部数据
    async getArticles() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/getdata/article",
      });
      this.ArcticleData = data.data;
    },
  },
  created() {
    this.getArticles(); //创建项目时调用
  },
};
</script>
<style lang="less" scoped>
</style>