<template>
  <el-form ref="linkForm" :model="linkForm" :rules="rules" label-width="80px">
    <el-form-item label="名称">
      <el-input v-model="linkForm.name" prop="name"></el-input>
    </el-form-item>
    <el-form-item label="首页地址">
      <el-input v-model="linkForm.home" prop="home"></el-input>
    </el-form-item>
    <el-form-item label="图标地址">
      <el-input v-model="linkForm.logo" prop="logo"></el-input>
    </el-form-item>
    <el-form-item label="描述">
      <el-input v-model="linkForm.describe" prop="describe"></el-input>
    </el-form-item>
    <el-from-item>
        <el-button type="primary" size="small" @click="submitAdd">确认添加</el-button>
        
    </el-from-item>
  </el-form>
</template>

<script>
import {mapMutations} from "vuex"
export default {
  name: "addlink",
  data() {
    return {
      linkForm: {
        naem: "",
        home:"",
        logo:"",
        describe:"",
      },
      rules:{
            name:[{ required: true}],
                home:[{ required: true, type:"url", message:"你输入的内容不是一个url"}],
                logo:[{ required: true, type:"url", message:"你输入的内容不是一个url"}],
                describe:[{ required: true}]
      }
    };
  },
  methods:{
    ...mapMutations(["nonticeAddlink"]),
     async submitAdd(){
       this.$refs.linkForm.validate(async v=>{
           if(v){
               let {data} = await this.$axios({
                   method:"POST",
                   url:"/administer/link/addData",
                   data:this.linkForm
               })
                 console.log(data);
               this.$refs.linkForm.resetFields();//添加后清空表单
               this.nonticeAddlink()//每当添加一条数据时调用一次修改vuex的数据
           }else{
               return false;
           }
       })
      }
  }
};
</script>

<style lang="less" scoped>
</style>