<template>
  <div>
    <el-table :data="linData" style="width: 100%">
      <el-table-column label="名称" width="180">
        <template #default="scope">
          <el-input v-model="scope.row.name"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="首页" width="180">
        <template #default="scope">
          <el-input v-model="scope.row.home"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="描述" width="180">
        <template #default="scope">
          <el-input v-model="scope.row.describe"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="图标" width="180">
        <template #default="scope">
          <el-input v-model="scope.row.logo"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template #default="scope">
          <el-button size="mini" @click="handleEdit(scope.row)" type="primary"
            >修改</el-button
          >
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import {mapState} from "vuex"

export default {
  name: "ManlgLink",
  data() {
    return {
      linData: [],
    };
  },
    computed:{
    ...mapState(["addLink"])
  },
    watch:{
      //监视vuex中state的数据发生改变调用
    addLink(){
          this.getlinkData();
    }
  },
  methods: {
    async getlinkData() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/getdata/link",
      });
      this.linData = data.data;
    },
    //修改友链
    async handleEdit(item) {
      let { _id, name, home, logo, describe } = item;
      let { data } = await this.$axios({
        method: "POST",
        url: "/administer/link/updata",
        data: {
          _id,
          name,
          home,
          logo,
          describe,
        },
      });
      console.log(data);
      this.$message.success(data.message);
    },
    //删除友链
    async handleDelete(item) {
      let { data } = await this.$axios({
        method: "delete",
        url: "/administer/link/delet",
        data: {
          _id: item._id,
        },
      });
      if (data.code) return this.$message.error(data.message);
      console.log(data);
      this.$message.success(data.message);
      this.getlinkData();
    },
  },
  created() {
    this.getlinkData();
  },
};
</script>

<style lang="less" scoped>
</style>