<template>
  <div>
    <article>
      <el-tabs v-model="activeName">
        <el-tab-pane label="发表文章" name="first">
        <Addactice></Addactice>
            </el-tab-pane>
        <el-tab-pane label="文章管理" name="second">
            <ArctcleLink></ArctcleLink></el-tab-pane>
        <el-tab-pane label="添加友链" name="third">
            <AddLink></AddLink>
              </el-tab-pane>
        <el-tab-pane label="友链管理" name="fourth">
          <ManlgLink></ManlgLink> </el-tab-pane>
        <el-tab-pane label="反馈收集" name="five">
            <Contactmanger></Contactmanger></el-tab-pane >
      </el-tabs>
    </article> 
  </div>
</template>

<script>
import AddLink from "../components/admin/addlink.vue"
import ManlgLink from "../components/admin/ManlgLink.vue"
import Addactice from "../components/admin/Addactice.vue"
import ArctcleLink from "../components/admin/ArctcleLink.vue"
import Contactmanger from "../components/admin/Contactmanger.vue"
export default {
  name: "Admin",
  data(){
      return{
          activeName:"five"
      }
  },

  components:{AddLink,ManlgLink,Addactice,ArctcleLink,Contactmanger},
  methods:{
      async isCheck(){
          let {data} = await this.$axios({
              method:"POST",
              url:"/administer/isCheck"
          })
          if(data.code){//不是管理员重定向路由
              this.$router.replace("/");
             this.$message.error(data.message)

          }
          else{
                  this.$message.success(data.message);
          }
      }
  } ,
  created(){
      this.isCheck();
  }
};
</script>

<style lang="less" scoped>
article{
     width: 100%;
  margin-bottom: 15px;
  padding: 25px 20px;
  background-color: #fff;
  font-family: Quicksand, Microsoft YaHei, sans-serif;
  box-sizing: border-box;
  box-shadow: 0 0 4px #ddd;
}
</style>