<template>
  <el-form
    ref="articleForm"
    :model="articleForm"
    :rules="rules"
    label-width="100px"
  >
    <el-form-item label="标题">
      <el-input
        v-model="articleForm.title"
        placeholder="可以自动获取上传md文件的文件名"
      ></el-input>
    </el-form-item>
    <el-form-item label="描述">
      <el-input type="textarea" v-model="articleForm.describe"></el-input>
    </el-form-item>
    <el-form-item label="md文件上传">
      <el-upload
        class="upload-demo"
        drag
        :action="`${baseUrl}/administer/article/md`"
        :auto-upload="false"
        :with-credentials="true"
        :limit="1"
        ref="mdFrom"
        :on-change="mdChange"
        :before-remove="removeTitle"
        :on-success="upMddata"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
      </el-upload>
    </el-form-item>
    <el-form-item label="上传文章封面">
      <el-upload
        class="avatar-uploader"
        :action="baseUrl + '/administer/article/cover'"
        :show-file-list="false"
        :with-credentials="true"
        :auto-upload="false"
        ref="coverFrom"
        :on-change="coverChange"
        :on-success="coverUpdata"
      >
        <img v-if="imageUrl" :src="imageUrl" class="avatar" />
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </el-form-item>
    <el-from-item>
      <el-button type="primary" @click="submitFrom">文章提交</el-button>
    </el-from-item>
  </el-form>
</template>     
<script>
export default {
  name: "Addactice",
  data() {
    return {
      articleForm: {
        title: "",
        describe: "",
        mdUrl: "",
        coverUrl: "",
      },
      imageUrl: "",
    };
  },
  methods: {
    // 上传md文件，添加文件、上传成功和上传失败时都会被调用
    mdChange(file, fileList) {
      if (file.status === "ready") {
        //为待上传
        if (/^(.+)\.md$/.test(file.name)) {
          //只有文件后缀名为.md时   正则校验
          this.articleForm.title = RegExp.$1; //组/子项  上面使用。+后自动将正则匹配到regexp中 .$1 获取上一次正则匹配到的（）匹配到的字符串内容
        } else {
          this.$message.error("请上传文件后缀名为md文件类型");
          fileList.pop(); //从尾部删除非md文件类型的文件
        }
      }
    },
    //点击x后清空标题
    removeTitle(file) {
      this.articleForm.title = "";
    },
    //成功上传封面文件或者失败后进行调用
    coverChange(file, fileList) {
      if (file.status === "ready") {
        const isJPG = file.raw.type === "image/jpeg";
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error("上传头像图片只能是 JPG 格式!");
        }
        if (!isLt2M) {
          this.$message.error("上传头像图片大小不能超过 2MB!");
        }
        if (isJPG && isLt2M) {
          this.imageUrl = URL.createObjectURL(file.raw); //设置缩略图的路径
        }
        return isJPG && isLt2M;
      } else {
        fileList.pop(); //封面图片不符合标准时删除里面的数据
      }
    },
    //点击提交按钮
    submitFrom() {
      //先上传md文件
      this.$refs.mdFrom.submit(); //调用上传文件的控件 发起请求到后端
    },
    //md文件上传成功后
    upMddata(res) {
      //上传失败
      if (res.code) return this.$message.error(res.message);
      //md文件上传成功后上传封面
      this.articleForm.mdUrl = res.mdUrl; //得到md文件地址存储到mdUrl中
      if (this.imageUrl) {
        //有封面z
        this.$refs.coverFrom.submit(); //调用上传文件的控件 发起请求到后端
      } else {
        this.fromSbmit();
      }
    },
    // 封面文件上传成功后会自动调用
    coverUpdata(res) {
      this.articleForm.coverUrl = res.coverUrl; //得到封面地址存储到coverUrl
      this.fromSbmit();
    },
    //发起表单数据到后端
    async fromSbmit() {
      let { data } = await this.$axios({
        method: "POST",
        url: "/administer/article/add",
        data: this.articleForm,
      });

      this.$message.success(data.message);
      this.$router.push(`/actire/${data.data.id}`)//跳转到id对应的详情页
    },
  },
};
</script>

<style lang="less" scoped>
/deep/.el-upload {
  position: relative;
  overflow: hidden;
  border: 1px dashed #d9d9d9;
  cursor: pointer;
  border-radius: 6px;
  .avatar {
    max-width: 178px;
    max-height: 178px;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
}
</style>