<template>
  <div>
    <el-table :data="ArcticleData" style="width: 100%">
      <el-table-column label="日期" width="180">
        <template #default="scope">
          <span>{{ new Date(scope.row.date).toLocaleString() }}</span>
        </template>
      </el-table-column>
        <el-table-column prop="name" label="名称" width="70"> </el-table-column>
        <el-table-column prop="email" label="邮箱" width="190"> </el-table-column>
        <el-table-column prop="message" label="内容" width="150"> </el-table-column>
          <el-table-column label="是否处理">
            <template #default="scope">
               <el-switch
                    style="display: block"
                    v-model="scope.row.raading"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    active-text="已阅读"
                    inactive-text="未阅读"
                    @change="switchState(scope.row)"
                >
                </el-switch>
            </template>
        </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "Contactmanger",
  data() {
    return {
      ArcticleData: [],
    };
  },
  methods: {
      //点击开关时触发到数据库中修改
     async switchState(item){
         let {data} =await this.$axios({
             method:"POST",
             url:"/administer/contact/updata",
             data:{
                 id:item._id,
                 raading:item.raading
             }
         })
       this.$message.success(data.code)
      },
    async getArticles() {
      let { data } = await this.$axios({
        method: "GET",
        url: "/getdata/contact",
      });
      this.ArcticleData = data.data;
      console.log(this.ArcticleData);
    },
  },
  created() {
    this.getArticles();
  },
};
</script>

<style lang="less" scoped>
</style>